            // }
            let label = this.getLabel(shownum);
            label.getComponent("addNode");
            var scaleBoom = cc.scaleTo(0.2, 1.3);
            var scalesm = cc.scaleTo(0.2, 1);
            var mtop = cc.moveBy(0.2, cc.p(0, 10));
